@import url(https://fonts.googleapis.com/css?family=Lakki+Reddy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.lyric-body {
    display: flex;
    justify-content: center;
    font-size: xx-large;
    color: whitesmoke;
    font-family: 'Lakki Reddy', cursive;
    margin: 5% 20% 5% 20%;   
    text-shadow: 2px 2px rgb(91, 109, 92); 
    text-align: center;
}
.input-field {
    display: flex;
    background-image: -webkit-gradient(linear, right bottom, left top, from(#000000),  color-stop(#000000), color-stop(#06110a), color-stop(#12eb6b), to(#12eb6b));
    background-image: linear-gradient(to left top, #000000,  #000000, #06110a, #12eb6b, #12eb6b);   
    justify-content: center;
    padding: 3%;
    font-family: 'Acme', sans-serif;
    height: 20pc;
}
input.form-control {
    width: 70vh;
    background-color: rgb(200, 233, 243);
    margin: 10px;
}
input.form-control::-webkit-input-placeholder { 
    color: rgb(80, 78, 78);
    opacity: 1; 
}
input.form-control::-moz-placeholder { 
    color: rgb(80, 78, 78);
    opacity: 1; 
}
input.form-control:-ms-input-placeholder { 
    color: rgb(80, 78, 78);
    opacity: 1; 
}
input.form-control::-ms-input-placeholder { 
    color: rgb(80, 78, 78);
    opacity: 1; 
}
input.form-control::placeholder { 
    color: rgb(80, 78, 78);
    opacity: 1; 
}
